import { Injectable } from '@angular/core';
import { Diagnostic } from '@ionic-native/diagnostic';
import { AlertController } from 'ionic-angular';


/*
  Generated class for the BrowseProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class BrowseProvider {
  //baseUrl = "https://qa-mobile.cafepress.com";
  // baseUrl = "https://cafepress.com";
  baseUrl = "https://mobile.cafepress.com";
  app_version = "1.0.16";
  enableBranchSDK = false;
  permissionState : boolean = false;
  permissionStateMsg = "";
  


  camAndGallearyErrText = "It looks like your privacy settings are preventing us from accessing your camera/gallery. Please enable camera/gallery permissions from app settings and try again.";
  camErrText = "It looks like your privacy settings are preventing us from accessing your camera. Please enable camera permissions from app settings and try again";
  gallaryErrText = "It looks like your privacy settings are preventing us from accessing your gallary Please select read & write permissions for gallery from app settings and try again";

  camAndGalleryPermissions = {
    "cameraPermissions" : false,
    "gallaryPermissions" : false,
    "errMsg" : ""
  };

  constructor(private diagnostic: Diagnostic, public alertCtrl : AlertController ) {

  }

  /**
   * Check ios camera and gallery peermissions
   */
  checkIosPermissions() {
    console.log('permissions called');
    this.diagnostic.getCameraAuthorizationStatus().then(status => {
      console.log('permissions called' + status);
      if (status == this.diagnostic.permissionStatus.GRANTED) {
        this.camAndGalleryPermissions.cameraPermissions = true;
      }
      else if (status == this.diagnostic.permissionStatus.DENIED) {
        this.camAndGalleryPermissions.cameraPermissions = false;
        this.camAndGalleryPermissions.errMsg = this.camErrText;
        this.showPermissionDeniedAlert(this.camErrText);

      }
      else if (status == this.diagnostic.permissionStatus.NOT_REQUESTED || status.toLowerCase() == 'not_determined') {
        this.diagnostic.requestCameraAuthorization().then(authorisation => {
          console.log("Authorization request for camera  was " + (authorisation == this.diagnostic.permissionStatus.GRANTED ? "granted" : "denied"));
          let authorisationState;
          if (authorisation == "granted") {
            this.camAndGalleryPermissions.cameraPermissions =true;
          }
          else {
            this.camAndGalleryPermissions.cameraPermissions = false;
            this.camAndGalleryPermissions.errMsg = this.camErrText;
            this.showPermissionDeniedAlert(this.camErrText);

          }
          //this.camaraRollRequest(authorisationState);
        });
      }

    })
    this.getGallaryPermissions();
  }

  /*
  * Request gallary permissions
  */
 

  /*
  * Check gallaey permissions
  */
  getGallaryPermissions() {
    this.diagnostic.getCameraRollAuthorizationStatus().then(status => {
      switch (status) {
        case this.diagnostic.permissionStatus.NOT_REQUESTED:
          console.log("Permission not requested");
          this.diagnostic.requestCameraRollAuthorization().then(status => {
            console.log("Authorization request for Gallery roll was " + (status == this.diagnostic.permissionStatus.GRANTED ? "granted" : "denied"));
            if(status === "granted"){
            this.camAndGalleryPermissions.gallaryPermissions = true;
            }
            else{
              this.camAndGalleryPermissions.gallaryPermissions = false;
              this.camAndGalleryPermissions.errMsg = this.gallaryErrText;
              this.showPermissionDeniedAlert(this.gallaryErrText);
            }
          }).catch(err => {
            console.log('camara roll error');
          })
          break;
        case this.diagnostic.permissionStatus.DENIED:
          console.log("Permission denied");
          this.camAndGalleryPermissions.gallaryPermissions = false;
          this.camAndGalleryPermissions.errMsg = this.gallaryErrText;
          this.showPermissionDeniedAlert(this.gallaryErrText);

          break;
        case this.diagnostic.permissionStatus.GRANTED:
          console.log("Permission granted");
          this.camAndGalleryPermissions.gallaryPermissions = true;
         break;
      }
    }).catch(err => {

    })
    if(!(this.camAndGalleryPermissions.cameraPermissions && this.camAndGalleryPermissions.gallaryPermissions)){
      this.camAndGalleryPermissions.errMsg = this.camAndGallearyErrText;
      this.showPermissionDeniedAlert(this.camAndGallearyErrText);

    }
  }
  show

  setAppPermissionStatus(state) {
    this.permissionState = state;
    console.log("permission state" + this.permissionState);
  }

  getAppPermissionStatus() {
    let error = false;
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        if (error) {
          reject();
        } else {
          console.log("permission state get methos---" + this.permissionState);
          resolve(this.permissionState);

        }
      }, 1000);
    });
    // return this.permissionState; 
  }

  checkOnClick(){
    console.log('permissions onclick called');
    this.diagnostic.requestCameraRollAuthorization().then(status => {
      console.log("Authorization request for Gallery roll was " + (status == this.diagnostic.permissionStatus.GRANTED ? "granted" : "denied"));
      if(status == "granted"){
        console.log('permission onclick granted');
        alert('Camara Granted');
      }
      else{
        console.log('permission onclick deny');
        alert('Camara denied');
      }
    })
  }
  showPermissionDeniedAlert(permissionText) {
    alert(permissionText);
//     let alert = this.alertCtrl.create({
//         title: 'Important', 
//         message: permissionText,
//         buttons: [
//          {
//             text: 'Dismiss',
//             role: 'cancel',
//             handler: () => {
//                 console.log('Cancel clicked');
//             }
//         },
//       {
//           text: 'Allow',
//           handler: () => {
//          //Switch to app settings
//           this.diagnostic.switchToSettings().then((succuss) => {
//           console.log('camara succuss ');
//       })
//     }
//   }]
//  });
//  alert.present();
}


}



