import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { SpinnerDialog } from "@ionic-native/spinner-dialog";
import { AndroidPermissions } from '@ionic-native/android-permissions';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { ThemeableBrowser, ThemeableBrowserOptions, ThemeableBrowserObject } from '@ionic-native/themeable-browser/ngx';
import { MysettingsPage } from '../pages/mysettings/mysettings';
import { SettingsDetailPage } from '../pages/settings-detail/settings-detail';
import { BrowseProvider } from '../providers/browse/browse';
import { UserAgent } from '@ionic-native/user-agent';
import { CallNumber } from '@ionic-native/call-number';
import { Network } from '@ionic-native/network';
import { AppRate } from '@ionic-native/app-rate';
import { IonicStorageModule } from '@ionic/storage';
import { Diagnostic } from '@ionic-native/diagnostic';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Sim } from '@ionic-native/sim';

@NgModule({
  declarations: [
    MyApp,
    HomePage,MysettingsPage,SettingsDetailPage,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,MysettingsPage,SettingsDetailPage
  ],
  providers: [
    StatusBar,
    SplashScreen, InAppBrowser, SpinnerDialog, CallNumber, Network,
    { provide: ErrorHandler, useClass: IonicErrorHandler }, Camera,Sim,
    BrowseProvider, UserAgent, AppRate, AndroidPermissions, ThemeableBrowser, Diagnostic
  ]
})
export class AppModule {}



