import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Nav, Platform } from 'ionic-angular';
import { CallNumber } from '@ionic-native/call-number';
import { BrowseProvider } from '../../providers/browse/browse';
import { UserAgent } from '@ionic-native/user-agent';
import { ThemeableBrowser, ThemeableBrowserOptions, ThemeableBrowserObject } from '@ionic-native/themeable-browser/ngx';
import { SpinnerDialog } from '@ionic-native/spinner-dialog';
import { Sim } from '@ionic-native/sim';

/**
 * Generated class for the MysettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

var browser;

@IonicPage()
@Component({
  selector: 'page-mysettings',
  templateUrl: 'mysettings.html',
})
export class MysettingsPage {
  urlLink: any;
  public app_version: string = "";
  simPresent : boolean = false;
  options: ThemeableBrowserOptions = {

    toolbar: {
      height: 45,
      color: '#ffffff'
    },
    title: {
      color: '#ffffff',
      showPageTitle: false,
    },

    closeButton: {
      wwwImage: 'assets/imgs/icon_close.png',
      align: 'right',
      event: 'closePressed'
    },
    backButtonCanClose: true,
    disableAnimation: true,
  }

  constructor(public navCtrl: NavController, public platform: Platform, public navParams: NavParams, private callNumber: CallNumber, public sim: Sim,
    private alertCtrl: AlertController, private nav: Nav, private browser: BrowseProvider, private themeableBrowser: ThemeableBrowser, private userAgent: UserAgent, private spinnerDialog: SpinnerDialog) {
      this.sim.getSimInfo().then(info => {
        console.log('Sim info: ', JSON.stringify(info));
        if(info.carrierName != ""){
          this.simPresent = true;
        }
      }).catch(err => {
        console.log('Unable to get sim info: ', err);
      });
    this.urlLink = this.browser.baseUrl;
    this.app_version = this.browser.app_version;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MysettingsPage');
  }

  ionViewWillEnter() {
    window.close();
  }

  goSet() {
    this.navCtrl.push('HomePage');
  }

  openOutSideWebBrowser(url) {
    this.openUrlInBrowser(this.urlLink + url);
    //window.open(this.urlLink + url, "_system");
  }

  openUrlInBrowser(url) {
    browser = this.themeableBrowser.create(url, '_blank', this.options);
    browser.on("close_pressed").subscribe(e => {
      this.closrBrowser();
    });
    browser.on("loadstart").subscribe(e => {
      this.spinnerDialog.show();
    });
    browser.on("loadstop").subscribe(e => {
      this.spinnerDialog.hide();
    });
    browser.on("loaderror").subscribe(e => {
      this.spinnerDialog.hide();
    });
  }

  closrBrowser() {
    if (browser != null)
      browser.close();
  }

  openOtherLinks() {
    if (this.platform.is('ios'))
      window.open("itms-apps://itunes.apple.com/app/1024941703", "_system");
    else
      window.open("https://play.google.com/store/apps/details?id=com.cafepress.mobile.android", "_system");
  }

  launchDialer(n: string) {
    if(this.simPresent){
    this.callNumber.callNumber(n, true)
      .then(() => console.log('Launched dialer!'))
      .catch(() => console.log('Error launching dialer'));
    }
    else{
      console.log('Call not supported');
    }
  }

  mailto(email: string) {
    window.open(`mailto:${email}`, '_system');
  }
}



